import React, { useState } from 'react';
import { BarChart, LineChart } from "@mui/x-charts";
import { useEffectOnce } from "react-use";

type ChartData = {
  date: string;
  fcrN: number;
  fcrDUp: number;
  fcrDDown: number;
  affrUp: number;
  affrDown: number;
  mffrUp: number;
  mffrDown: number;
}

const DailyLines = ({
  data,
  series,
}: {
  data: ChartData[];
  series: any[];
}) => {
  return (
    <LineChart
      height={960}
      dataset={data}
      series={series}
      xAxis={[{ dataKey: 'date', scaleType: 'band' }]}
    />
  );
}

const MonthlyBars = ({
  data,
  series,
}: {
  data: ChartData[];
  series: any[];
}) => {

  const dataDict: Record<string, ChartData[]> = {};
  const yearAndMonths: string[] = [];
  for (const daily of data) {
    const yearAndMonth = daily.date.substring(0, 7);
    if (!dataDict.hasOwnProperty(yearAndMonth)) {
      dataDict[yearAndMonth] = [];
      yearAndMonths.push(yearAndMonth);
    }
    dataDict[yearAndMonth].push(daily);
  }

  const dataset: ChartData[] = [];
  for (const yearAndMonth of yearAndMonths) {
    dataset.push({
      date: yearAndMonth,
      fcrN: Math.round(dataDict[yearAndMonth].reduce((acc, daily) => acc + daily.fcrN, 0) / dataDict[yearAndMonth].length),
      fcrDUp: Math.round(dataDict[yearAndMonth].reduce((acc, daily) => acc + daily.fcrDUp, 0) / dataDict[yearAndMonth].length),
      fcrDDown: Math.round(dataDict[yearAndMonth].reduce((acc, daily) => acc + daily.fcrDDown, 0) / dataDict[yearAndMonth].length),
      affrUp: Math.round(dataDict[yearAndMonth].reduce((acc, daily) => acc + daily.affrUp, 0) / dataDict[yearAndMonth].length),
      affrDown: Math.round(dataDict[yearAndMonth].reduce((acc, daily) => acc + daily.affrDown, 0) / dataDict[yearAndMonth].length),
      mffrUp: Math.round(dataDict[yearAndMonth].reduce((acc, daily) => acc + daily.mffrUp, 0) / dataDict[yearAndMonth].length),
      mffrDown: Math.round(dataDict[yearAndMonth].reduce((acc, daily) => acc + daily.mffrDown, 0) / dataDict[yearAndMonth].length)
    });
  }

  return (
    <BarChart
      height={960}
      dataset={dataset}
      series={series}
      xAxis={[{ dataKey: 'date', scaleType: 'band' }]}
    />
  );
}

export const App = () => {
  const years: number[] = [2023, 2024];

  const [chartData, setChartData] = useState<ChartData[]>([]);

  const loadChart = async () => {
    const data: ChartData[] = [];
    for (const year of years) {
      const csv: string = await (await fetch(`https://sonicdog.se/data/${year}.csv`)).text();
      const yearData: ChartData[] = csv.split("\n")
        .filter(line => line.length > 0)
        .map((line) => {
          const [date, fcrN, fcrDUp, fcrDDown, affrUp, affrDown, mffrUp, mffrDown] = line.split(",");
          return {
            date: date,
            fcrN: parseFloat(fcrN),
            fcrDUp: parseFloat(fcrDUp),
            fcrDDown: parseFloat(fcrDDown),
            affrUp: parseFloat(affrUp),
            affrDown: parseFloat(affrDown),
            mffrUp: parseFloat(mffrUp),
            mffrDown: parseFloat(mffrDown),
          };
        });
      data.push(...yearData);
    }
    setChartData(data);
  }

  useEffectOnce(() => {
    loadChart();
  });

  return (
    <>
      <DailyLines
        data={chartData}
        series={[
          { dataKey: 'fcrN', label: 'FCR-N', showMark: false },
          { dataKey: 'fcrDUp', label: 'FCR-D Up', showMark: false },
          { dataKey: 'fcrDDown', label: 'FCR-D Down', showMark: false },
        ]} />

      <DailyLines
        data={chartData}
        series={[
          { dataKey: 'affrUp', label: 'aFFR Up (SE4)', showMark: false },
          { dataKey: 'affrDown', label: 'aFFR Down (SE4)', showMark: false },
          { dataKey: 'mffrUp', label: 'mFFR Up (SE4)', showMark: false },
          { dataKey: 'mffrDown', label: 'mFFR Down (SE4)', showMark: false }
        ]} />

      <MonthlyBars
        data={chartData}
        series={[
          { dataKey: 'fcrN', label: 'FCR-N', stack: 0 },
          { dataKey: 'fcrDUp', label: 'FCR-D Up', stack: 0 },
          { dataKey: 'fcrDDown', label: 'FCR-D Down', stack: 0 },
          { dataKey: 'affrUp', label: 'aFFR Up (SE4)', stack: 0 },
          { dataKey: 'affrDown', label: 'aFFR Down (SE4)', stack: 0 },
          { dataKey: 'mffrUp', label: 'mFFR Up (SE4)', stack: 0 },
          { dataKey: 'mffrDown', label: 'mFFR Down (SE4)', stack: 0 }
        ]} />
    </>
  );
}
